details[open] {
  summary {
    &:after {
      content: "";
      background: url(../images/down-arrow.png) no-repeat 0px 3px;
    }
  }
}

details {
  summary {
    &:after {
      content: "";
      background: url(../images/right-arrow-breadcrumb.png) no-repeat 0px 0px;
      width: 14px;
      height: 11px;
      display: inline-block;
      margin-left: 35px;
    }
  }
}

